import React from 'react'
import Modal from 'react-bootstrap/Modal'


export default function FeedAlgorithmInfoModal(props) {

    return (
        <Modal show={props.isOpen}
            onHide={props.hideModal}
            size="lg"
        >
            <Modal.Header closeButton>
                <Modal.Title><strong>Configuraes do Algoritmo</strong></Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <h3>Como o nosso algoritmo deve funcionar?</h3>
                <p>
                    Os critrios presentes na barra lateral (Interesses em comum, Semelhana de idade...) so utilizados para calcularmos a
                    relevncia de um perfil em relao a outro. Sabendo disso, voc pode escolher o peso de cada critrio e assim, personalizar o
                    algoritmo que calcular a relevncia dos outros perfis.
                    <br />
                    <br />
                    Exemplo: Caso voc mude o peso do critrio "Interesses em comum" para 100 e todos os outros para 0, os perfis da pgina "Encontrar perfis" 
                    sero ordenados levando em considerao apenas a quantidade de interesses em comum que o usurio tem com voc.
                    <br />
                    <br />
                    A relevncia de um post  calculada baseada na relevncia do autor do mesmo e dos perfis que curtiram a publicao. Ou seja,
                    ao decidir como a relevncia de um perfil ser calculada, voc est personalizando o algoritmo que decidir quais posts devem aparecer para voc
                    e em que ordem.
                </p>
            </Modal.Body>
        </Modal>
    )
}