import React from 'react'
import Modal from 'react-bootstrap/Modal'


export default function RecomendedProfilesInfoModal(props) {
    const profileSlug = props.profileSlug

    return (
        <Modal show={props.isOpen}
            onHide={props.hideModal}
            size="lg"
        >
            <Modal.Header closeButton>
                <Modal.Title><strong>Perfis recomendados</strong></Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <h3>Como decidimos quais perfis aparecem na barra lateral?</h3>
                <p>
                    Os perfis que esto na barra lateral so considerados os perfis que mais "batem" com @{profileSlug}.
                    Ou seja, so perfis que possuem bastante afinidade com o usurio.
                </p>

                <br />
                <br />

                <h3>Como  calculada a relevncia de um perfil para @{profileSlug}?</h3>
                <p>
                    Os critrios presentes nas configuraes do algoritmo (Interesses em comum, Semelhana de idade...) so utilizados para calcularmos a
                    relevncia de um perfil em relao a outro. Sabendo disso, voc pode escolher o peso de cada critrio e assim, personalizar o
                    algoritmo que calcular a relevncia dos perfis.
                </p>
            </Modal.Body>
        </Modal>
    )
}