import React from 'react'

import InterestsInput from './InterestsInput'

export default function PrivateInterests(props) {

    return (
        <div className="p-2">
            <h3>Interesses privados</h3>
            <p>
                Os interesses privados no so visveis para os outros usurios.
                Eles servem para aumentar a preciso das recomendaes de perfis que
                possuem o mximo de afinidade possvel com voc.
            </p>
            <InterestsInput type="private" myProfile={props.myProfile} setInterests={props.setInterests} />
        </div>
    )
}